body {
  min-width: 320px; }

.container {
  max-width: 1560px;
  margin: 0 auto;
  padding: 0 30px;
  &--blog {
    padding-top: 48px; }
  @include r(1023) {
    padding: 0 15px;
    &--blog {
      padding-top: 48px; } }
  @include r(500) {
    &--blog {
      padding-top: 0; } } }

.mob-hidden {
  @include r(500) {
    display: none; } }

.mob-visible {
  @include r(500) {
    display: inherit; } }
