.blog-main-slider {
  margin: 60px 0 80px;
  @include r(500) {
    margin: 0 -15px 50px; }
  &__inner {
    display: flex;
    align-items: flex-start;
    min-height: 500px;
    padding-left: 30px;
    @include r(1200) {
      min-height: 350px; }
    @include r(1023) {
      min-height: 360px;
      padding-left: 15px; }
    @include r(767) {
      align-items: center;
      justify-content: flex-end; } }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-position: 50% 50%;
    object-fit: cover;
    font-family: 'object-fit: cover object-position: 50% 50%'; }
  &__text {
    position: relative;
    margin-top: 156px;
    margin-left: auto;
    padding: 20px 182px 20px 20px;
    font-family: 'Oxygen', sans-serif;
    font-size: rem(37);
    font-weight: 700;
    line-height: 1.2;
    color: #fafafa;
    max-width: 860px;
    background: linear-gradient(90deg, rgba(143, 138, 210, 0.7) 50.05%, rgba(255, 255, 255, 0) 100%);
    @include r(1200) {
      margin-top: 103px;
      margin-bottom: 30px;
      max-width: 670px;
      padding-right: 44px;
      font-size: rem(28); }
    @include r(767) {
      margin: 0; }
    @include r(500) {
      max-width: 500px;
      padding: 15px 23px 15px 15px;
      font-size: rem(21);
      background: rgba($violet, 0.7); } }
  .slick-dots {
    position: absolute;
    bottom: 15px;
    left: 0;
    right: 0;
    text-align: center;
    li {
      display: inline-block;
      margin: 0 6px;
      button {
        font-size: 0;
        width: 12px;
        height: 12px;
        background: $violet;
        border-radius: 50%; }
      &.slick-active {
        button {
          width: 16px;
          height: 16px;
          background: $yellow;
          border: 1px solid #33669A; } } } } }

.blog-section-title {
  position: relative;
  margin-bottom: 50px;
  &:after {
    content: '';
    display: block;
    margin-top: 15px;
    height: 2px;
    background: $brand-dark-navy;
    background: radial-gradient(61.7% 321.59% at 48% 50%, rgba(52, 84, 253, 0.4) 0%, rgba(101, 52, 206, 0.4) 100%, rgba(66, 51, 85, 0.4) 100%, rgba(0, 0, 0, 0.18) 100%), linear-gradient(95.11deg, #33669A 6.92%, #23C2C0 71.47%); } }

.latest {
  margin-top: 50px;
  &__wrapper {
    overflow: hidden; }
  &__cards {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
    @include r(1200) {
      margin: 0 -10px; }
    @include r(767) {
      margin: 0 auto;
      max-width: 600px; } } }

.latest-card {
  display: flex;
  align-items: flex-end;
  position: relative;
  margin: 0 20px 35px;
  padding: 20px 20px 30px;
  background-color: #c4c4c4;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fafafa;
  border-radius: 5px;
  min-height: 396px;
  flex-basis: calc(50% - 40px);
  &:nth-last-child(n+5),
  &:nth-last-child(n+5) ~ div {
    &:not(.latest-card--big) {
      flex-basis: calc(33.33% - 40px); } }
  @include r(1200) {
    min-height: 308px;
    margin: 0 10px 20px;
    padding: 15px 15px 20px;
    flex-basis: calc(33.33% - 20px); }
  @include r(767) {
    margin: 0 0 20px;
    padding: 45px 10px 12px;
    flex-basis: 100%;
    min-height: 260px; }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba($brand-dark-navy, 0.65);
    background: linear-gradient(358.93deg, rgba(7, 14, 65, 0.65) 0.87%, rgba(37, 77, 155, 0.6) 99.06%);
    background-size: cover;
    background-position: 50% 50%;
    border-radius: 5px; }
  &--big {
    flex-basis: calc(50% - 40px);
    min-height: 396px;
    &:before {
      background: linear-gradient(358.93deg, rgba(7, 14, 65, 0.65) 0.87%, rgba(37, 77, 155, 0) 99.06%); }
    @include r(1200) {
      flex-basis: calc(50% - 20px);
      min-height: 260px; }
    @include r(767) {
      flex-basis: 100%;
      &:before {
        background: linear-gradient(358.93deg, rgba(7, 14, 65, 0.65) 0.87%, rgba(37, 77, 155, 0.6) 99.06%); } } }
  &__info {
    position: relative;
    width: 100%;
    @include r(767) {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;
      justify-content: space-between; } }
  &__title {
    max-width: 645px;
    margin-bottom: 4px;
    @include r(1200) {
      font-size: rem(25); }
    @include r(767) {
      font-size: rem(21); } }
  &__date {
    margin-top: 10px;
    float: left;
    font-size: rem(20);
    font-weight: 300;
    @include r(1200) {
      font-size: rem(18); }
    @include r(767) {
      float: none;
      margin-top: 0;
      font-size: rem(16); } } }

.tags {
  margin-left: 20px;
  font-size: 0;
  float: right;
  @include clr;
  @include r(767) {
    align-self: flex-end;
    float: none;
    margin-left: 0; } }

.tag {
  display: inline-block;
  margin: 10px 15px 10px 0;
  padding: 4px 8px 6px;
  background: #2BB826;
  color: #fff;
  box-shadow: 0px 2px 5px rgba(33, 36, 44, 0.5);
  border-radius: 3px;
  font-size: rem(20);
  font-weight: 300;
  line-height: 1;
  &:last-child {
    margin-right: 0; }
  @include r(1200) {
    font-size: rem(18); }
  @include r(767) {
    font-size: rem(16); } }
