html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

.icon-moon { width: 0.71em; height: 1em; fill: initial; }

.icon-sign-in { width: 1em; height: 1em; fill: #254D9B; }

.icon-sun { width: 1em; height: 1em; fill: initial; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

body { font-family: 'Noto Sans JP', sans-serif; font-weight: 400; }

.title-h1, .title-h2, .title-h3, .title-h4, .title-h5 { font-family: 'Oxygen', sans-serif; font-weight: 700; }

.title-h1 { text-align: center; font-size: 2.8125rem; line-height: 1.1; }

@media only screen and (max-width: 1200px) { .title-h1 { font-size: 2.5rem; } }

@media only screen and (max-width: 1023px) { .title-h1 { font-size: 2.1875rem; } }

.title-h2 { font-size: 2.4375rem; line-height: 1.2; }

@media only screen and (max-width: 1200px) { .title-h2 { font-size: 2.125rem; } }

@media only screen and (max-width: 1023px) { .title-h2 { font-size: 1.8125rem; } }

.title-h3 { font-size: 1.9375rem; line-height: 1.2; }

@media only screen and (max-width: 1200px) { .title-h3 { font-size: 1.625rem; } }

@media only screen and (max-width: 1023px) { .title-h3 { font-size: 1.5rem; } }

.title-h4 { font-size: 1.5625rem; line-height: 1.2; }

@media only screen and (max-width: 1200px) { .title-h4 { font-size: 1.25rem; } }

.title-h4 { font-size: 1.25rem; line-height: 1.2; }

@media only screen and (max-width: 1200px) { .title-h4 { font-size: 1.125rem; } }

body { min-width: 320px; }

.container { max-width: 1560px; margin: 0 auto; padding: 0 30px; }

.container--blog { padding-top: 48px; }

@media only screen and (max-width: 1023px) { .container { padding: 0 15px; }
  .container--blog { padding-top: 48px; } }

@media only screen and (max-width: 500px) { .container--blog { padding-top: 0; } }

@media only screen and (max-width: 500px) { .mob-hidden { display: none; } }

@media only screen and (max-width: 500px) { .mob-visible { display: inherit; } }

.header { position: relative; }

.header__inner { display: flex; justify-content: space-between; align-items: flex-start; max-width: 1660px; margin: 0 auto; padding: 25px 30px; }

@media only screen and (max-width: 1200px) { .header__inner { padding: 22px 30px; } }

@media only screen and (max-width: 1023px) { .header__inner { padding: 15px 15px; } }

.header__actions { display: flex; align-items: center; padding-top: 4px; }

@media only screen and (max-width: 767px) { .header__actions { padding-top: 1px; } }

.logo { max-width: 285px; }

@media only screen and (max-width: 1200px) { .logo { max-width: 250px; } }

@media only screen and (max-width: 767px) { .logo { max-width: 200px; } }

@media only screen and (max-width: 500px) { .logo { max-width: 150px; } }

.logo__pic { max-width: 100%; }

.sign-in { margin-right: 95px; font-family: 'Oxygen', sans-serif; font-weight: 700; font-size: 0; color: #254D9B; transition: color 0.15s linear; }

@media only screen and (max-width: 1200px) { .sign-in { margin-right: 70px; } }

@media only screen and (max-width: 767px) { .sign-in { margin-right: 30px; } }

@media only screen and (max-width: 500px) { .sign-in { margin-right: 0; } }

.sign-in:hover { color: #3c6ed0; }

.sign-in:hover .icon { fill: #3c6ed0; }

.sign-in .icon { vertical-align: middle; font-size: 2.5rem; transition: fill 0.15s linear; }

@media only screen and (max-width: 500px) { .sign-in .icon { font-size: 1.875rem; } }

.sign-in span { margin-left: 12px; display: inline-block; vertical-align: middle; font-size: 1.1875rem; }

@media only screen and (max-width: 500px) { .sign-in span { font-size: 1.0625rem; } }

.header-link { position: relative; display: inline-block; padding: 9px 0; margin-right: 50px; font-family: 'Oxygen', sans-serif; font-size: 1.1875rem; font-weight: 700; text-decoration: none; color: #2BB826; transition: color 0.15s linear; }

.header-link:after { content: ''; position: absolute; bottom: 0; left: 0; width: 100%; height: 1px; background-color: #2BB826; transition: width 0.2s ease; }

.header-link:hover { color: #40d63b; }

.header-link:hover:after { width: 0; }

.theme-switcher { position: relative; font-size: 0; }

@media only screen and (max-width: 500px) { .theme-switcher { position: absolute; z-index: 1; top: 100%; right: 12px; margin-top: 10px; padding: 6px 12px 6px 9px; box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.2); border-radius: 5px; background: #fff; } }

.theme-switcher:after { content: ''; position: absolute; width: 1px; background: #000; top: 0; left: 50px; bottom: 0; }

@media only screen and (max-width: 500px) { .theme-switcher:after { top: 5px; bottom: 5px; left: 54px; } }

.theme-switcher__button { background: none; vertical-align: middle; }

.theme-switcher__button svg { display: block; font-size: 2.125rem; }

.theme-switcher__button--light { margin-right: 1rem; }

@media only screen and (max-width: 500px) { .theme-switcher__button--light { margin-right: 0.7rem; } }

.theme-switcher__button--light svg { fill: #FDAC34; }

.theme-switcher__button--dark { margin-left: 1.4rem; }

@media only screen and (max-width: 500px) { .theme-switcher__button--dark { margin-left: 1rem; } }

.theme-switcher__button--dark svg { font-size: 1.5rem; }

.blog-main-slider { margin: 60px 0 80px; }

@media only screen and (max-width: 500px) { .blog-main-slider { margin: 0 -15px 50px; } }

.blog-main-slider__inner { display: flex; align-items: flex-start; min-height: 500px; padding-left: 30px; }

@media only screen and (max-width: 1200px) { .blog-main-slider__inner { min-height: 350px; } }

@media only screen and (max-width: 1023px) { .blog-main-slider__inner { min-height: 360px; padding-left: 15px; } }

@media only screen and (max-width: 767px) { .blog-main-slider__inner { align-items: center; justify-content: flex-end; } }

.blog-main-slider__img { position: absolute; top: 0; left: 0; width: 100%; height: 100%; object-position: 50% 50%; object-fit: cover; font-family: 'object-fit: cover object-position: 50% 50%'; }

.blog-main-slider__text { position: relative; margin-top: 156px; margin-left: auto; padding: 20px 182px 20px 20px; font-family: 'Oxygen', sans-serif; font-size: 2.3125rem; font-weight: 700; line-height: 1.2; color: #fafafa; max-width: 860px; background: linear-gradient(90deg, rgba(143, 138, 210, 0.7) 50.05%, rgba(255, 255, 255, 0) 100%); }

@media only screen and (max-width: 1200px) { .blog-main-slider__text { margin-top: 103px; margin-bottom: 30px; max-width: 670px; padding-right: 44px; font-size: 1.75rem; } }

@media only screen and (max-width: 767px) { .blog-main-slider__text { margin: 0; } }

@media only screen and (max-width: 500px) { .blog-main-slider__text { max-width: 500px; padding: 15px 23px 15px 15px; font-size: 1.3125rem; background: rgba(143, 138, 210, 0.7); } }

.blog-main-slider .slick-dots { position: absolute; bottom: 15px; left: 0; right: 0; text-align: center; }

.blog-main-slider .slick-dots li { display: inline-block; margin: 0 6px; }

.blog-main-slider .slick-dots li button { font-size: 0; width: 12px; height: 12px; background: #8F8AD2; border-radius: 50%; }

.blog-main-slider .slick-dots li.slick-active button { width: 16px; height: 16px; background: #FFD700; border: 1px solid #33669A; }

.blog-section-title { position: relative; margin-bottom: 50px; }

.blog-section-title:after { content: ''; display: block; margin-top: 15px; height: 2px; background: #33669A; background: radial-gradient(61.7% 321.59% at 48% 50%, rgba(52, 84, 253, 0.4) 0%, rgba(101, 52, 206, 0.4) 100%, rgba(66, 51, 85, 0.4) 100%, rgba(0, 0, 0, 0.18) 100%), linear-gradient(95.11deg, #33669A 6.92%, #23C2C0 71.47%); }

.latest { margin-top: 50px; }

.latest__wrapper { overflow: hidden; }

.latest__cards { display: flex; flex-wrap: wrap; margin: 0 -20px; }

@media only screen and (max-width: 1200px) { .latest__cards { margin: 0 -10px; } }

@media only screen and (max-width: 767px) { .latest__cards { margin: 0 auto; max-width: 600px; } }

.latest-card { display: flex; align-items: flex-end; position: relative; margin: 0 20px 35px; padding: 20px 20px 30px; background-color: #c4c4c4; background-position: center center; background-size: cover; background-repeat: no-repeat; color: #fafafa; border-radius: 5px; min-height: 396px; flex-basis: calc(50% - 40px); }

.latest-card:nth-last-child(n+5):not(.latest-card--big), .latest-card:nth-last-child(n+5) ~ div:not(.latest-card--big) { flex-basis: calc(33.33% - 40px); }

@media only screen and (max-width: 1200px) { .latest-card { min-height: 308px; margin: 0 10px 20px; padding: 15px 15px 20px; flex-basis: calc(33.33% - 20px); } }

@media only screen and (max-width: 767px) { .latest-card { margin: 0 0 20px; padding: 45px 10px 12px; flex-basis: 100%; min-height: 260px; } }

.latest-card:before { content: ''; position: absolute; top: 0; left: 0; right: 0; bottom: 0; background: rgba(51, 102, 154, 0.65); background: linear-gradient(358.93deg, rgba(7, 14, 65, 0.65) 0.87%, rgba(37, 77, 155, 0.6) 99.06%); background-size: cover; background-position: 50% 50%; border-radius: 5px; }

.latest-card--big { flex-basis: calc(50% - 40px); min-height: 396px; }

.latest-card--big:before { background: linear-gradient(358.93deg, rgba(7, 14, 65, 0.65) 0.87%, rgba(37, 77, 155, 0) 99.06%); }

@media only screen and (max-width: 1200px) { .latest-card--big { flex-basis: calc(50% - 20px); min-height: 260px; } }

@media only screen and (max-width: 767px) { .latest-card--big { flex-basis: 100%; }
  .latest-card--big:before { background: linear-gradient(358.93deg, rgba(7, 14, 65, 0.65) 0.87%, rgba(37, 77, 155, 0.6) 99.06%); } }

.latest-card__info { position: relative; width: 100%; }

@media only screen and (max-width: 767px) { .latest-card__info { display: flex; flex-direction: column; height: 100%; width: 100%; justify-content: space-between; } }

.latest-card__title { max-width: 645px; margin-bottom: 4px; }

@media only screen and (max-width: 1200px) { .latest-card__title { font-size: 1.5625rem; } }

@media only screen and (max-width: 767px) { .latest-card__title { font-size: 1.3125rem; } }

.latest-card__date { margin-top: 10px; float: left; font-size: 1.25rem; font-weight: 300; }

@media only screen and (max-width: 1200px) { .latest-card__date { font-size: 1.125rem; } }

@media only screen and (max-width: 767px) { .latest-card__date { float: none; margin-top: 0; font-size: 1rem; } }

.tags { margin-left: 20px; font-size: 0; float: right; }

.tags:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 767px) { .tags { align-self: flex-end; float: none; margin-left: 0; } }

.tag { display: inline-block; margin: 10px 15px 10px 0; padding: 4px 8px 6px; background: #2BB826; color: #fff; box-shadow: 0px 2px 5px rgba(33, 36, 44, 0.5); border-radius: 3px; font-size: 1.25rem; font-weight: 300; line-height: 1; }

.tag:last-child { margin-right: 0; }

@media only screen and (max-width: 1200px) { .tag { font-size: 1.125rem; } }

@media only screen and (max-width: 767px) { .tag { font-size: 1rem; } }
